<template>
    <div class="naviWrap">
        <compass :active="!dirLocked" @offset="(offset=$event)" />
        <div class="sideGroup">
            <div class="_group">
                <button type="button" class="_trigger" @click="setScale(-1)"><i class="cubeic-remove"></i></button>
                <button type="button" class="_trigger" @click="setScale(1)"><i class="cubeic-add"></i></button>
            </div>
            <div class="_group">
                <button type="button" 
                class="_trigger" 
                :class="{'unread': messageList.length}"
                @click="showGlobalMessage=true"><i class="cubeic-notification"></i></button>
            </div>
        </div>

        <div class="view" id="viewCont">
            <div class="mapCont" id="mapCont"
                :style="{'backgroundImage': `url(${layoutBG})`, 'transform': `rotateZ(${offset}deg) scale(${scaleValue})`}">
                <!-- 路径层 -->
                <div class="layer">
                    <svg width="1000" height="1000" xmlns="http://www.w3.org/2000/svg" v-if="naviLines && naviLines.length">
                        <!-- <polyline v-for="(item,index) in pathData" :key="'showPath'+index" :points="item.map(e=>{return e.x+','+e.y}).join(' ')" 
              class="basePath"/> -->
                        <!-- 导航结果展示 -->
                        <polyline v-for="(item, index) in naviLines" :key="'navline'+index" 
                        :points="item.map(e=>{return e.x+','+e.y}).join(' ')" 
                        class="resultPath" 
                        :class="{is_current: index===naviStepNumber-1}" />
                    </svg>
                </div>
                <!-- 建筑层 -->
                <div class="layer">
                    <svg width="1000" height="1000" xmlns="http://www.w3.org/2000/svg" id="svgElements" @click.self="resetSvg();resetNav();">
                        <template v-for="(item,index) in polygons">
                            <polygon :key="'polygon'+index" 
                                :id="'b_'+item.attrDataX+'_'+item.attrDataY"
                                :class="{isBlock: item.block, svgArea: true, active: currentPolygon && (currentPolygon.attrTitle===item.attrTitle)}" 
                                :style="item.type" 
                                :points="item.points" 
                                :data-x="item.attrDataX" 
                                :data-y="item.attrDataY" 
                                :title="item.attrTitle" 
                                @click="clickElement(item)" />
                        </template>
                    </svg>
                </div>
                <!-- 漂浮层 -->
                <div class="layer mapFloat">
                    <template v-for="(item,index) in buildingPolygons">
                        <span :key="'float'+index" class="_title" 
                        :style="{'left': (item.attrDataX-item.attrTitle.length*10)+'px', 'top': item.attrDataY+'px', 'transform': `rotateZ(${-offset}deg)`}">{{item.attrTitle}}</span>
                    </template>
                </div>
            </div>
        </div>
        <!-- 商家信息 -->
        <cube-popup type="nav-step-1" :mask="false" ref="navStep1Box" position="bottom">
            <div class="cube-extend-popup-content">
                <slot>
                    <div class="extend-info" v-if="currentPolygon">
                        <h3>{{currentPolygon.attrTitle}}</h3>
                        <p>{{currentPolygon.desc}}</p>
                        <ul class="_message">
                            <li v-for="(item,index) in currentPolygon.message" :key="'sm'+index">
                                <i class="cubeic-volume"></i> {{item.title}}
                            </li>
                        </ul>
                    </div>
                    <div class="flex-row" style="border:1px solid #eee">
                        <div class="flex-1">
                            <cube-button icon="cubeic-back" :light="true" @click="naveSections.start = currentPolygon;navStep2()">从这里出发</cube-button>
                        </div>
                        <div class="flex-1">
                            <cube-button icon="cubeic-arrow" :light="true" @click="naveSections.end = currentPolygon;navStep2()">到这里去</cube-button>
                        </div>
                    </div>
                </slot>
            </div>
        </cube-popup>
        <!-- 导航-start -->
        <cube-popup type="nav-step-2" :mask="false" ref="navStep2Box" position="bottom">
            <div class="cube-extend-popup-content">
                <slot>
                    <div class="extend-info">
                        <div class="flex-row align-center m">
                            起点：
                            <div class="flex-1 _t">
                                <template v-if="naveSections.start">
                                    {{naveSections.start.attrTitle}}
                                </template>
                                <template v-else>
                                    "在地图上选择或者" <cube-button :inline="true" @click="openSearch()">搜索</cube-button>
                                </template>
                            </div>
                        </div>
                        <div class="flex-row align-center m">
                            终点：
                            <div class="flex-1 _t">
                                <template v-if="naveSections.end">
                                    {{naveSections.end.attrTitle}}
                                </template>
                                <template v-else>
                                    在地图上选择或者
                                    <cube-button icon="cubeic-search" :inline="true" @click="openSearch()">搜索</cube-button>
                                </template>
                            </div>
                        </div>
                    </div>

                    <cube-button :primary="true" :disabled="!naveSections.start || !naveSections.end" @click="search()">开始导航</cube-button>
                </slot>
            </div>
        </cube-popup>
        <!-- 导航-end -->
        <cube-popup type="nav-step-3" :mask="false" ref="navStep3Box" position="bottom">
            <div class="cube-extend-popup-content">
                <slot>
                    <div class="flex-row aimMarker">
                        <div class="flex-1">
                            <p class="el"><i class="cubeic-aim"></i>起点：<span class="_point">{{naveSections.start && naveSections.start.attrTitle}}</span></p>
                        </div>
                        <div class="flex-1">
                            <p class="el"><i class="cubeic-navigation"></i>终点：<span class="_point">{{naveSections.end && naveSections.end.attrTitle}}</span></p>
                        </div>
                    </div>
                    <cube-slide ref="slide" style="margin-bottom:10px" :data="navResult" :autoPlay="false" :loop="false" @change="naviStepNumber=$event+1">
                        <cube-slide-item v-for="(item, index) in navResult" :key="index">
                            <div class="nav-item extend-info">{{item.direction}}{{item.deflection}}{{item.orientation}}{{item.distence}}{{item.shopInfo}}</div>
                        </cube-slide-item>
                    </cube-slide>

                    <cube-button :primary="true" @click="resetNav();">关闭</cube-button>
                </slot>
            </div>
        </cube-popup>
        <!-- 消息列表弹窗 -->
        <div class="globalMessageDialog" v-show="showGlobalMessage">
            <div class="_layer flex-col">
                <header class="_hd">
                    <h1>通知公告</h1> <i class="cubeic-close _close" @click="showGlobalMessage=false"></i>
                </header>
                <div class="flex-1 scrollcont">
                    <ul class="cube-scroll-list shopList">
                        <li v-for="(item,index) in globalMessage" :key="'gml'+index"
                            class="cube-scroll-item border-bottom-1px">
                            <h3 class="_tit"><i class="_ico cubeic-volume"></i> {{item.title}}</h3>
                            <div class="_msg">{{item.message}}</div>
                            <div class="_foot">
                                <span class="_shop" @click="showGlobalMessage=false;aimTo(item.attrTitle)">{{item.attrTitle}} <i class="cubeic-aim"></i></span>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
        <!-- footnav -->
        <div class="footNav">
            <ul class="flex-row">
                <li class="flex-1">
                    <cube-button :light="true" @click="$refs.tip.show()">
                        <i class="ion">&#xe622;</i>
                        发现
                    </cube-button>
                    <cube-tip ref="tip" direction="bottom" style="left:0;bottom: 100%;">敬请期待</cube-tip>
                </li>
                <li class="flex-1">
                    <cube-button :light="true" @click="$router.push('/maps')">
                        <i class="ion">&#xe600;</i>
                        楼层
                    </cube-button>
                </li>
                <li class="flex-1">
                    <cube-button :light="true" @click="openSearch()">
                        <i class="ion">&#xe64f;</i>
                        搜索
                    </cube-button>
                </li>
                <li class="flex-1">
                    <cube-button :light="true" @click="toggleLock()">
                        <i class="ion">&#xe6c6;</i>
                        旋转
                    </cube-button>
                    
                </li>
            </ul>
        </div>

    </div>
</template>

<script>
import * as util from '@/assets/util';
import * as message from "@/api/message";

import maps from "@/assets/maps/index";
import * as core from "@/assets/core";

import VConsole from "vconsole";
if(process.env.NODE_ENV==='development'){
    const vConsole = new VConsole();
}

export default {
    components: {
        compass: resolve => require(["@/components/compass.vue"], resolve)
    },
    data() {
        return {
            showGlobalMessage: false,
            dirLocked: false,
            maps,
            offset: 0,
            scaleValue: 1,
            viewCont: null,
            mainDom: null,
            buildingSVG: null,
            currentPolygon: null,
            polygons: [],
            pathData: [],
            pathResult: null,
            scaleQeueu: [],
            scaleIndex: 2,
            naveSections: {
                start: null,
                end: null
            },
            navResult: [],
            naviStepNumber: 1,
            messageList: []
        };
    },
    computed: {
        globalMessage: function(){
            return this.messageList.filter(e => e.global)
        },
        naviLines: function() {
            let result = [];
            if (Array.isArray(this.pathResult) && this.pathResult.length) {
                this.pathResult.forEach((e, i) => {
                    if (i < this.pathResult.length - 1) {
                        let next = this.pathResult[i + 1];
                        result.push([e, next]);
                    }
                });
            }
            return result;
        },
        buildingPolygons: function(){
            return this.polygons.filter(e => !e.block)
        }
    },
    methods: {
        toggleLock: function(){
            if(this.dirLocked){
                this.dirLocked = false;
            }else{
                this.dirLocked = true;
                this.offset = 0;
            }
            this.$createToast({
                type: 'txt',
                txt: this.dirLocked ? "方向锁定" : "方向跟随"
            }).show()
        },
        aimTo: function(title){
            let shopObj = this.polygons.filter(e => e.attrTitle===title)
            if(shopObj.length){
                shopObj = shopObj[0]
                this.clickElement(shopObj, true)
            }
        },
        viewContPosi: function(top, left){
            this.viewCont.scrollTop = top
            this.viewCont.scrollLeft = left
        },
        setScale: function(step) {
            if (this.mainDom) {
                if (step > 0) {
                    if (this.scaleIndex < this.scaleQeueu.length - 1) {
                        this.scaleIndex++;
                    }
                } else if (step < 0) {
                    if (this.scaleIndex > 0) {
                        this.scaleIndex--;
                    }
                }
                this.scaleValue = this.scaleQeueu[this.scaleIndex];
                if(!step){
                    // init
                    let centerScroll = (1-this.scaleValue)*500
                    this.viewContPosi(centerScroll, centerScroll)
                }
            }
        },
        openSearch: function(){
            this.$router.push({name:'搜索', params: {items: this.buildingPolygons}})
        },
        resetNav: function() {
            this.hidePopInfo();
            this.naviStepNumber = 1;
            this.pathResult = [];
            this.navResult = [];
            this.naveSections.start = null;
            this.naveSections.end = null;
        },
        navStep2: function() {
            this.hidePopInfo();
            this.resetSvg()
            this.$nextTick(() => {
                this.$refs.navStep2Box.show();
            });
        },
        hidePopInfo: function() {
            this.$refs.navStep1Box.hide();
            this.$refs.navStep2Box.hide();
            this.$refs.navStep3Box.hide();
        },
        clickElement: function(item, location) {
            this.resetSvg();
            
            // 提取商家公告
            let message = this.messageList.filter(e => e.attrTitle===item.attrTitle);

            this.currentPolygon = Object.assign({message}, item)
            
            if (this.naveSections.start) {
                //拾取终点
                this.naveSections.end = item;
                this.$nextTick(() => {
                    this.$refs.navStep2Box.show();
                });
            } else if (this.naveSections.end) {
                //拾取起点
                this.naveSections.start = item;
                this.$nextTick(() => {
                    this.$refs.navStep2Box.show();
                });
            } else {
                //选择商家
                this.$refs.navStep1Box.show();
                // 定位
                if(location){
                    setTimeout(() => {
                        let polygonElement = document.getElementById('b_'+item.attrDataX+'_'+item.attrDataY)
                        if(polygonElement){
                            let pos = polygonElement.getBoundingClientRect()
                            let scrollTop = pos.top - window.innerHeight/2 + this.viewCont.scrollTop + 100; // 商户信息面板高度补偿
                            let scrollLeft = pos.left - window.innerWidth/2 + this.viewCont.scrollLeft + 100; // 商户自身宽度补偿
                            this.viewContPosi(scrollTop, scrollLeft)
                        }
                        
                    }, 200)
                }
            }
        },
        resetSvg: function() {
            this.currentPolygon = null;
        },
        fetchData: function(mapname) {
            let targetMap = this.maps.filter(e => e.name===mapname)
            if(targetMap.length){
                this.polygons = targetMap[0].map;
                this.pathData = targetMap[0].path;
                this.layoutBG = targetMap[0].layout;
            }else{
                console.warn(mapname + ' is not exist')
            }
            //test
            // this.naveSections.start = {
            //   connectPoint: {
            //     x:223,
            //     y:126
            //   }
            // }
            // this.naveSections.end = {
            //   connectPoint: {
            //     x: 451,
            //     y: 631
            //   }
            // }
            // this.search();
        },
        search: function() {
            let nav_start = this.naveSections.start.connectPoint;
            let nav_end = this.naveSections.end.connectPoint;
            if (!nav_end || !nav_start || !nav_start.x || !nav_end.x) {
                return console.warn("参数错误");
            }
            //寻路
            core.startSearch(this.pathData, nav_start, nav_end, data => {
                if (Array.isArray(data)) {
                    this.hidePopInfo();
                    core.navigate(data, navi => {
                        console.log(navi);
                        this.navResult = navi.result;
                        this.pathResult = navi.path;
                        this.$refs.navStep3Box.show();
                    });
                } else {
                    this.$createToast({
                        txt: "搜索失败",
                        type: "error",
                        time: 1000
                    }).show();
                    this.resetSvg();
                    this.resetNav();
                }
            });
        },
        fetchMessage: function(){
            message.list().then(res => {
                this.messageList = res.data.data
            })
        }
    },
    created: function() {
        let mapkey = this.$route.params.name
        if(mapkey){
            this.fetchData(mapkey);
        }

        this.fetchMessage()

        // 搜素事件
        util.on('searchDone', item => {
            this.clickElement(item, true)
        })
    },
    mounted: function() {
        this.viewCont = document.getElementById('viewCont');
        this.mainDom = document.getElementById("mapCont");
        this.buildingSVG = document.getElementById("svgElements");
        let clientWidth = window.innerWidth;
        let minScale = clientWidth / 1000;
        let partWidth = parseInt((1000 - clientWidth) / 4);
        let scaleQeueu = [
            minScale,
            (clientWidth + partWidth) / 1000,
            (clientWidth + partWidth * 2) / 1000,
            (clientWidth + partWidth * 3) / 1000,
            1
        ];
        this.scaleQeueu = scaleQeueu;
        this.scaleIndex = 2;
        this.setScale(0);
        
    }
};
</script>

