<template>
  <div class="flex-col searchPage">
    <cube-input v-model="value" autofocus></cube-input>
    <div class="flex-1">
      <cube-scroll>
        <ul class="cube-scroll-list shopList ">
          <li v-for="(item,index) in searchResult" :key="'search'+index"
            class="cube-scroll-item border-bottom-1px"
            v-html="item.html"
            @click="submit(item)"></li>
        </ul>
      </cube-scroll>
    </div>
  </div>
</template>

<script>
import * as util from '@/assets/util';

export default {
  data () {
    return {
      value: '',
      items: []
    }
  },
  computed: {
    searchResult: function(){
      let keywords = this.value.trim()
      if(keywords && this.items.length){
        let titleMatch = this.items.filter(e => {
          return e.attrTitle.indexOf(keywords)>-1
        }).map(e => {
          e.html = '<h3 class="_tit">'+e.attrTitle.replace(keywords, `<em>${keywords}</em>`)+'</h3>'+
            '<div class="_msg">'+e.desc+'</div>'
          return e
        })

        let descMatch = this.items.filter(e => {
          return e.attrTitle.indexOf(keywords)===-1
        }).filter(e => {
          return e.desc && e.desc.indexOf(keywords)>-1
        }).map(e => {
          e.html = '<h3>'+e.attrTitle+'</h3>'+
            '<div>'+e.desc.replace(keywords, `<em>${keywords}</em>`)+'</div>'
          return e
        })
        
        return titleMatch.concat(descMatch)
      }else{
        return []
      }
    }
  },
  methods: {
    submit: function(item){
      this.value = ''
      util.emit('searchDone', item)
      this.$router.go(-1)
    }
  },
  created: function(){
    if(this.$route.params.items){
      this.items  = this.$route.params.items;
    }
  }
}
</script>

<style scoped>
.searchPage{height: 100%;}

</style>
